<template>
  <div :class="keyboardClass"></div>
</template>

<script>
import Keyboard from "simple-keyboard";
import english from "simple-keyboard-layouts/build/layouts/english";
import russian from "simple-keyboard-layouts/build/layouts/russian";

import "simple-keyboard/build/css/index.css";

export default {
  name: "SimpleKeyboard",

  props: {
    keyboardClass: {
      default: "simple-keyboard",
      type: String,
    },
    input: {
      type: String,
    },
    inputName: {
      type: String,
    },
  },

  data: () => ({
    keyboard: null,
  }),

  mounted() {
    this.keyboard = new Keyboard({
      onChange: this.onChange,
      onKeyPress: this.onKeyPress,
      inputName: this.inputName,
      theme: "hg-theme-default hg-layout-default myTheme",
      ...russian,
    });
  },

  methods: {
    onChange(input) {
      if (this.inputName === "phone" || this.inputName === "recphone") {
        input = input.replace(/\D/g, "");
        this.$emit("onChange", input);
      } else {
        this.$emit("onChange", input);
      }
    },

    onKeyPress(button) {
      this.$emit("onKeyPress", button);

      /**
       * If you want to handle the shift and caps lock buttons
       */
      if (button === "{shift}" || button === "{lock}") this.handleShift();
    },

    handleShift() {
      let currentLayout = this.keyboard.options.layoutName;
      let shiftToggle = currentLayout === "default" ? "shift" : "default";

      this.keyboard.setOptions({
        layoutName: shiftToggle,
      });
    },
  },

  watch: {
    inputName(inputName) {
      this.keyboard.setOptions({ inputName });

      if (inputName === "email") {
        this.keyboard.setOptions({ ...english });
      } else {
        this.keyboard.setOptions({ ...russian });
      }
    },

    input(input) {
      this.keyboard.setInput(input);
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.simple-keyboard {
  color: #000;
  font-size: 24px;
}

.simple-keyboard.hg-theme-default.myTheme {
  background: rgba(196, 196, 196, 0.47);
  padding: 20px;
  border-radius: 20px;
}

.simple-keyboard.hg-theme-default .hg-button {
  background: rgba(196, 196, 196, 0.9) !important;
  color: white;
}

.lang-change {
  width: 40px !important;
  max-width: 40px;
}
</style>
