<template>
  <div class="btn-container">
    <button type="button" class="btn" :class="getClass()" @click="onClick">
      <slot />
    </button>
  </div>
</template>

<script>
export default {
  props: {
    btnType: {
      type: String,
      default: "main",
    },
    action: {
      type: String,
      default: "",
    },
  },

  methods: {
    getClass() {
      if (this.btnType === "simpleText") {
        return "btn_simple-text";
      } else if (this.btnType === "bigText") {
        return "btn_big-text";
      } else {
        return "btn_main";
      }
    },

    onClick() {
      this.$emit(this.action);
    },
  },
};
</script>

<style scoped>
.btn-container {
  width: 100%;
  text-align: center;
}

.btn {
  display: inline-block;
  border: 0;
  cursor: pointer;
}

.btn_main {
  background: var(--second-color);
  color: var(--second-text-color);
  font-size: var(--main-btn-font-size);
  border-radius: var(--second-border-radius);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  padding: 10px 30px 12px;
}

.btn_simple-text {
  border: none;
  color: var(--second-color);
  font-size: 18px;
  background: none;
  font-weight: 300;
}

.btn_big-text {
  background: none;
  border: none;
  color: var(--second-color);
  font-size: 18px;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: 300;
}
</style>
