<template>
  <div class="input-cont">
    <input
      :id="name"
      :type="type"
      :placeholder="placeholder"
      :value="inputValue"
      class="input"
      :class="{ error: isError, focus: isFocus }"
      @focus="onInputFocus"
      @input="onInputChange"
      readonly
    />
  </div>
</template>

<script>
export default {
  props: {
    data: {
      type: Object,
    },
    errorInputs: {
      type: Array,
    },
    name: {
      type: String,
      required: true,
    },
    type: {
      type: String,
      default: "text",
    },
    placeholder: {
      type: String,
      default: "",
    },
    focusedInput: {
      type: String,
      default: "",
    },
  },

  data() {
    return {
      isError: false,
      isFocus: false,
    };
  },

  computed: {
    inputValue: function () {
      return this.data[this.name];
    },
  },

  watch: {
    errorInputs: {
      handler(value) {
        if (value.includes(this.name)) {
          this.isError = true;
        } else {
          this.isError = false;
        }
      },
    },

    focusedInput(value) {
      if (value === this.name) {
        this.isFocus = true;
      } else {
        this.isFocus = false;
      }
    },
  },

  methods: {
    onInputChange(input) {
      this.$emit("onInputChange", input);
    },

    onInputFocus(input) {
      this.$emit("onInputFocus", input);
    },
  },
};
</script>

<style scoped>
.input {
  width: 100%;
  max-width: 495px;
  height: 68px;
  display: flex;
  padding: 0 20px;
  margin-left: auto;
  margin-right: auto;
  align-items: center;
  text-align: center;
  border: 0px;
  font-size: 34px;
  color: var(--main-bg-color);
  border-radius: var(--main-border-radius);
  background: white;
  box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
  box-sizing: border-box;
}

.input.error {
  box-shadow: 0px 0px 0px 4px red;
}

.input:focus {
  outline: none !important;
}
.input.focus {
  -webkit-box-shadow: 0px 0px 0px 4px var(--second-color);
  -moz-box-shadow: 0px 0px 0px 4px var(--second-color);
  box-shadow: 0px 0px 0px 4px var(--second-color);
}
</style>
