<template>
  <div class="main-container">
    <ArrowsSvg class="arrows" :isActive="animActive" />

    <transition name="fade" mode="out-in">
      <component
        :is="currentStep"
        :data="data"
        :errorInputs="errorInputs"
        :focusedInput="focusedInput"
        class="step"
        @next="onNext"
        @back="onBack"
        @skip="onSkip"
        @backToMain="resetData"
        @onInputFocus="onInputFocus"
        @onInputChange="onInputChange"
      />
    </transition>

    <transition name="keyboard-fade">
      <div v-show="focusedInput" class="keyboard-container">
        <SimpleKeyboard
          @onChange="onChange"
          :input="data[focusedInput]"
          :inputName="focusedInput"
        />
      </div>
    </transition>
  </div>
</template>

<script>
import HelloStep from "@/components/HelloStep.vue";
import PresentStep from "@/components/PresentStep.vue";
import PersonalDataStep from "@/components/PersonalDataStep.vue";
import CompanyDataStep from "@/components/CompanyDataStep.vue";
import RecommendDataStep from "@/components/RecommendDataStep.vue";
import ThanksStep from "@/components/ThanksStep.vue";
import ArrowsSvg from "@/components/ArrowsSvg.vue";

import SimpleKeyboard from "@/components/SimpleKeyboard.vue";

const steps = [
  HelloStep,
  PresentStep,
  PersonalDataStep,
  CompanyDataStep,
  RecommendDataStep,
  ThanksStep,
];

function initialData() {
  return {
    name: "",
    email: "",
    phone: "",
    company: "",
    recname: "",
    recphone: "",
  };
}

export default {
  components: {
    HelloStep,
    PresentStep,
    PersonalDataStep,
    CompanyDataStep,
    RecommendDataStep,
    SimpleKeyboard,
    ThanksStep,
    ArrowsSvg,
  },

  data() {
    return {
      step: 0,
      data: initialData(),
      focusedInput: "",
      errorInputs: [],
      animActive: false,
    };
  },

  computed: {
    currentStep() {
      return steps[this.step];
    },
  },

  methods: {
    onNext() {
      this.focusedInput = "";

      if (!this.isValid()) {
        return;
      }

      this.runAnimation();
      this.step++;
    },

    onBack() {
      this.runAnimation();
      this.step--;
      this.focusedInput = "";
      this.errorInputs = [];
    },

    onSkip() {
      this.runAnimation();
      this.step++;
      this.focusedInput = "";
    },

    onChange(input) {
      this.data[this.focusedInput] = input;
      this.removeError(this.focusedInput);
    },

    onInputChange(input) {
      this.removeError(input.target.id);
      this.data[input.target.id] = input.target.value;
    },

    onInputFocus(input) {
      this.focusedInput = input.target.id;
    },

    resetData() {
      this.runAnimation();
      this.focusedInput = "";
      this.step = 0;
      this.data = initialData();
      this.errorInputs = [];
    },

    removeError(error) {
      this.errorInputs = this.errorInputs.filter((item) => item != error);
    },

    isValid() {
      if (this.step === 2) {
        if (this.data.name == "") {
          this.errorInputs.push("name");
        }

        if (this.data.email == "" || !this.data.email.includes("@")) {
          this.errorInputs.push("email");
        }

        if (this.data.phone == "") {
          this.errorInputs.push("phone");
        }
      }

      if (this.step === 3) {
        if (this.data.company == "") {
          this.errorInputs.push("company");
        }
      }

      return this.errorInputs.length == 0 ? true : false;
    },

    runAnimation() {
      this.animActive = true;
      setTimeout(() => {
        this.animActive = false;
      }, 1500);
    },
  },
};
</script>

<style scoped>
.main-container {
  max-width: 1920px;
  margin: 0 auto;
  padding: 90px 50px 0px;
  position: relative;
}

.arrows {
  position: absolute;
  top: -220px;
  right: 670px;
}

.step {
  margin-left: auto;
}

.keyboard-container {
  position: absolute;
  top: 40%;
  right: 700px;
  width: 900px;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity var(--animation-time);
}
.fade-enter, .fade-leave-to /* .fade-leave-active до версии 2.1.8 */ {
  opacity: 0;
}

.keyboard-fade-enter-active,
.keyboard-fade-leave-active {
  transition: opacity 0.4s;
}
.keyboard-fade-enter, .keyboard-fade-leave-to /* .fade-leave-active до версии 2.1.8 */ {
  opacity: 0;
}
</style>
